
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import * as types from '../store/mutation'

export default {
components: {
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Loading
},
mounted () {
this.failureShowToast = false
this.changeSuccessShow = false
this.showHeadTitle()
},
data () {
return {
showtest: false,
title: 'Login',
username: '',
password: '',
iconType: '',
failureText: '请重新登录！',
showHead: true,
showHead2: false,
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
loginSuccessShow: false,
loginSubmit: '登 录',
signUpSubmit: '注 册',
signUpLink: '/signUp',
LoginDisable: false
}
},
methods: {
showHeadTitle () {
},
goForgetPwd () {
this.$router.push('updatePassword')
},
change (val) {
},
onBlur (val) {
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
},
onHide () {
this.$router.push('/home')
},
loginAction () {

this.$vux.loading.show({
text: 'loading'
})
this.$http.post(this.$apis.login, {username: this.username, password: this.password}).then((res) => {
this.$vux.loading.hide()
if (res.code === 200) {


this.$store.commit(types.LOGIN, res.data.token)
this.$store.commit(types.USERID, res.data.userId) 
this.successText = res.msg
this.loginSuccessShow = true
} else {
this.changeFailureShow = true
this.failureText = res.msg
}
}).catch(function (error) {
this.$vux.loading.hide()
console.log('Error: ' + error)
})
}
}
}



